.App{
  text-align: center;
  /* border: 2px solid red; */
  background-image: linear-gradient(to left,rgb(166, 68, 240),rgba(28, 208, 217, 0.786));
  /* height: 700px; */
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.header{
  margin-top: -1px;
  color: white;
  /* border: 2px solid blue; */
  background-image: linear-gradient(to left,rgb(4, 39, 72),rgb(60, 147, 240));
  margin-left: 15%;
  margin-right: 15%;
  padding-top: 1%;
  padding-bottom: 1%;
  font-weight: bolder;
  font-size: 2rem;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.button{
  color: black;
  cursor: pointer;
  background-color: rgb(125, 235, 216);
  height: 50px;
  width: 150px;
  border-radius: 0rem 2rem 0rem 2rem;
  font-weight: bolder;
  font-size: 1.5rem;
  
}
.button:hover{
  color: rgb(0, 0, 0);
  background-image: linear-gradient(rgba(155, 155, 245, 0.33), rgb(246, 131, 246));
}

.head{
  /* border: 2px solid green; */
  color: wheat;
  background-image: linear-gradient(to left,blue,black);
}

.main-box{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* border: 2px solid black; */
  list-style: none;
  background-image: linear-gradient(to left,rgb(245, 198, 121),rgba(244, 129, 79, 0.971));
  margin-left: 5%;
  margin-right: 5%;
}

.image{ 
  cursor: pointer;
  padding-bottom: 2%;
  padding-top: 2%;
  padding-right: 2%;
  padding-left: 0%;
  margin-left: 0%;
  border-radius: 50%;
  height: 150px;
  width: 150px;
}

.content{
  margin-top: 0px;
  color: rgb(0, 0,0);
  font-size: 1.5rem;
}

.label{
  color: #080003;
  font-size: 1.5rem;
}

.content a{
  color: blue;
  text-decoration: none;
}

.card{
  background-color: rgb(140, 249, 249);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  /* align-items: center; */
  border-radius: 0rem 0.5rem 3rem 0.5rem;
  height: 20%;
  width: 30%;
  /* border: 2px solid rgb(11, 11, 11); */
  margin-top: 1%;
  margin-bottom: 1%;
  margin-left: 0.5%;
  box-shadow: 10px 10px 8px #080003;
}

